import $ from 'jquery';

const NAME = 'app_sidebar';
const VERSION = '1.0.0';
const JQUERY_NO_CONFLICT = $.fn[NAME];
const EVENT = 'click';


class Sidebar {
    constructor(element, button) {
        this._element = element;
        this._button = button;
        if (document.cookie.indexOf('sidebar_hidden') === -1) {
            this._state = 'OPEN';
        }else{
            this._state = 'CLOSED';
        }
        this._categories = $(this._element).find('#sidebar-categories');
        this.search();
        this._toggle();
    }

    static get VERSION() {
        return VERSION;
    }

    search() {
        const search_form = $(this._categories).find('#category_filter');

        search_form.on('input',function(e){
            e.preventDefault();

            let filter = function(search_input) {
                let selected_tree = search_form.find(search_input);
                console.log(selected_tree)
            };

            let search_filter = search_form.val();
            filter(search_filter);
        });
    }



    _toggle() {
        $(this._button).on(EVENT, function (e) {
            e.preventDefault();

            function cookie_solver() {
                if (document.cookie.indexOf('sidebar_hidden') === -1) {
                    document.cookie = 'sidebar_hidden=1; path=/';
                } else {
                    document.cookie = 'sidebar_hidden=; expires=Thu, 01 Jan 1970 00:00:00 GMT; path=/';
                }
            }

            if (this._state === 'CLOSED') {
                this._state = 'OPEN';
                cookie_solver();
                $('body').removeClass('sidebar-hidden');
            } else if (this._state === 'OPEN') {
                this._state = 'CLOSED';
                cookie_solver();
                $('body').addClass('sidebar-hidden');
            }
        })
    }

    static _jQueryInterface() {
        return new Sidebar(this, $('.sidebar-toggle'), $('.sidebar-mobile-toggle'));
    }
}

$.fn[NAME] = Sidebar._jQueryInterface;
$.fn[NAME].Constructor = Sidebar;
$.fn[NAME].noConflict = () => {
    $.fn[NAME] = JQUERY_NO_CONFLICT;
    return Sidebar._jQueryInterface;
};

export default Sidebar;