import $ from 'jquery';
import CMDB_FORM_IDENTIFIER_CLASS from './utils'

const NAME = 'password_toggle';
const VERSION = '1.0.0';
const JQUERY_NO_CONFLICT = $.fn[NAME];
const SELECTOR = {
    PASS_TOGGLE: '[data-toggle="cmdb_password"]'
};
const TOGGLE_INPUT = {
    PASS: 'password',
    TEXT: 'text'
};
const INPUT_TYPE = '[type="password"], [type="text"]';

class PasswordToggle {

    constructor(element) {
        this._element = element;
    }

    static get VERSION() {
        return VERSION
    }

    toggle_password(element) {
        let rootElement = this._element;
        if (element) {
            rootElement = this._getRootElement(element)
        }
        if (rootElement.type === TOGGLE_INPUT.TEXT) {
            rootElement.type = TOGGLE_INPUT.PASS
        } else if (rootElement.type === TOGGLE_INPUT.PASS) {
            rootElement.type = TOGGLE_INPUT.TEXT
        } else {
            rootElement.type = TOGGLE_INPUT.PASS
        }
    }

    _getRootElement(element) {
        parent = $(element).parent().parent().children(INPUT_TYPE)[0];
        return parent
    }

    static _jQueryInterface() {
        return this.each(function () {

            const $element = $(this);
            let data = $element.data();
            console.debug(data);
            if (!data) {
                let data = new PasswordToggle($(this));
                $element.data(data)
            }
        })
    }

    static _trigger(passwordInstance) {
        return function (event) {
            if (event) {
                event.preventDefault()
            }
            passwordInstance.toggle_password(this)
        }
    }
}

$(document).on(
    'click',
    CMDB_FORM_IDENTIFIER_CLASS.toString() + " " + SELECTOR.PASS_TOGGLE,
    PasswordToggle._trigger(new PasswordToggle($(this)))
);

$.fn[NAME] = PasswordToggle._jQueryInterface;
$.fn[NAME].Constructor = PasswordToggle;
$.fn[NAME].noConflict = () => {
    $.fn[NAME] = JQUERY_NO_CONFLICT;
    return PasswordToggle._jQueryInterface
};

export default PasswordToggle