import $ from 'jquery';
import Choices from 'choices.js';
import CMDB_FORM_IDENTIFIER_CLASS from './utils';

const NAME = 'select_choices';
const VERSION = '1.0.0';
const JQUERY_NO_CONFLICT = $.fn[NAME];
const SELECTOR = 'select';
const DATA_KEY = 'cmdb.select';

const DEFAULT_CONFIG = {
    searchEnabled: true,
    removeItemButton: true
};

class SelectChoices {

    constructor(element) {
        this._element = element;
        this.choice = new Choices(this._element, DEFAULT_CONFIG);
    }

    static get VERSION() {
        return VERSION
    }

    static _jQueryInterface() {
        return this.each(function () {
            let data = $(this).data(DATA_KEY);
            if (!data) {
                data = new SelectChoices(this);
                $(this).data(DATA_KEY, data)
            }
        })
    }

}

$.noConflict()(function () {
    const selectors = [].slice.call(document.querySelectorAll(CMDB_FORM_IDENTIFIER_CLASS + " " + SELECTOR));

    for (let i = selectors.length; i--;) {
        const $sel = $(selectors[i]);
        SelectChoices._jQueryInterface.call($sel, $sel.data());
    }
});

$.fn[NAME] = SelectChoices._jQueryInterface;
$.fn[NAME].Constructor = SelectChoices;
$.fn[NAME].noConflict = () => {
    $.fn[NAME] = JQUERY_NO_CONFLICT;
    return SelectChoices._jQueryInterface
};

export default SelectChoices