// STATIC SCSS IMPORT ENTRY
import './cmdb.scss';

// MODULES
import $ from 'jquery';
import Sidebar from './js/sidebar';
import PasswordToggle from './js/password_toggle';
import SelectChoices from './js/select_choices';
import Pace from './js/loader';
import QRCode from './js/qr_code';
import Choices from './js/vendors';
import './js/vendors';

(() => {
    if (typeof $ === 'undefined') {
        throw new TypeError('CMDB\'s JavaScript requires jQuery. jQuery must be included before CMDB\'s JavaScript.');
    }
})();

export {
    $,
    Sidebar,
    PasswordToggle,
    SelectChoices,
    Choices,
    QRCode
}

Pace.start();
$(document).ready(function () {
    // activate app sidebar
    $('#sidebar').app_sidebar();
    $('.data_table').each(function () {
        $(this).DataTable();
    });
    $('[data-toggle="tooltip"]').tooltip();
});